#spice1{
	padding: 15px;
	font-family: 'Six Caps', sans-serif;
	font-family: 'Michroma', sans-serif;
    font-size: 20vw;
    display: flex;
    margin-top: 38vh;
    margin-top: 8vmax;
    justify-content: center;
	color: black;
	font-weight: bold;
	-webkit-animation: spice1 3s ease-in-out infinite;
	        animation: spice1 3s ease-in-out infinite;
}
.scrolldown{
	position: absolute;
	bottom: -65px;
	left: 50%;
	width: 15px;
	height: 15px;
	margin-left: -15px;
	/* background: red; */
	transform: translateY(-80px) translateX(50%) rotate(45deg);
}
.scrolldown span{
	position: absolute;
	top: 0;
	left: 0;
	display: block;
	width: 100%;
	height: 100%;
	opacity: 0;
}
.scrolldown span{
    border-bottom: 2.5px solid black;
    border-right: 2.5px solid black;
    -webkit-animation: scroll 1s  ease-in-out infinite;
            animation: scroll 1s  ease-in-out infinite;
}
.scrolldown span:nth-child(3){
	transform: translate(15px,15px);
		-webkit-animation-delay: 0s;
		        animation-delay: 0s;
}
@font-face {
	font-family: Laceouss;
	src: url(/Laceouss_react-/static/media/Prida65.6ebf3a09.otf);
